body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #419ceb;
}

.container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.heading {
  font-size: 60px;
  letter-spacing: 3px;
  font-weight: normal;
  color: #fff;
  text-shadow: 1px 1px 10px #045194;
}

.text-one,
.text-two {
  color: #fff;
}

.text-one {
  font-size: 30px;
  margin-top: 30px;
}

.text-two {
  font-size: 20px;
  margin-top: 80px;
}

.btn-primary {
  background: #000;
  border: #000;
}

